.entry input,
.entry textarea {
    background-color: #181721;
    width: 400px;
    width: var(--input-width);
    border: 1px solid /*#211494*/rgb(3, 74, 3);
    border-radius: 4px;
    padding-left: 10px;
    color: #f5f5f5;
}

.light .entry input,
.light .entry textarea {
    background-color: #f5f5f5;
    color: #000;
    transition: all 0.5s;
}

.entry input:active,
.entry textarea:active,
.entry input:focus,
.entry textarea:focus {
    outline: none;
    background-color: #181721;
}

.light .entry input:active,
.light .entry textarea:active,
.light .entry input:focus,
.light .entry textarea:focus {
    outline: none;
    background-color: #f5f5f5;
}

.entry input {
    min-height: 40px;
}

.entry textarea {
    padding-top: 10px;
    resize: none;
}

.entry>div:not(.btn, .options) {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
}

.entry>div:not(.btn) label {
    margin-bottom: 5px;
}

.options {
    padding-top: 20px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 400px;
    width: var(--input-width);
}

.options div {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 10px;
}

.options div label {
    overflow: hidden;
}

[type='radio'] {
    padding-top: 5px;
}

[type='radio']:checked,
[type='radio']:not(:checked) {
    position: absolute;
    left: -9999px;
}

[type='radio']:checked+label,
[type='radio']:not(:checked)+label {
    position: relative;
    padding-left: 28px;
    cursor: pointer;
    line-height: 20px;
    display: inline-block;
    color: #666;
}

[type='radio']:checked+label:before,
[type='radio']:not(:checked)+label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 18px;
    height: 18px;
    border: 1px solid #ddd;
    border-radius: 100%;
    background: transparent;
}

[type='radio']:checked+label:after,
[type='radio']:not(:checked)+label:after {
    content: '';
    width: 12px;
    height: 12px;
    background: #6253e8;
    position: absolute;
    top: 4px;
    left: 4px;
    border-radius: 100%;
    transition: all 0.2s ease;
}

[type='radio']:not(:checked)+label:after {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0);
}

[type='radio']:checked+label:after {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
}

.btn {
    cursor: pointer;
    background-color: /*#6253e8*/rgb(125, 221, 125);
    width: calc(calc(400px / 2) + 20px);
    width: calc(calc(var(--input-width) / 2) + 20px);
    min-height: 35px;
    display: grid;
    place-items: center;
    border-radius: 5px;
    margin-top: 10px;
}

.light .btn {
    color: #fff;
}
